<template>
  <div>
    <Content style="width: 100%; background-color:rgb(237,237,237); padding-top:20px;">
      <Row type="flex" :gutter="16">
        <Col  span="8"  v-for="(item,index) in sever8" :key="index">
          <div>
            <p style="margin-bottom: 50px">
              <span><img :src="item.src" style="height: 25px; width: 25px" /></span>
              <span v-show="item.name!==''" class="name"> {{item.name}}</span>
              <span v-show="item.title!==''" class="title"> {{item.title}}</span>
            </p>
          </div>
        </Col>
      </Row>
    <div class="sever_foot">
    <span v-for="(item,index) in dblink" class="spanlink" :key="index"><a :href='item.src'>{{item.name}}</a> </span>
  </div>
      <Col  span="24" >
        <p style="height:30px;background-color:rgb(217,217,217) ; color:#5e5e5e; line-height: 30px; font-size: 12px">ICP16001175</p>
      </Col>
</Content>
  </div>
</template>
<script>
  export default {
    name: 'fooTer',
    data() {
      return {
        sever8:[
          {name:'', title:'010-57170315', src:require('../../assets/sever.png') },
          {name:'', title:'8', src:require('../../assets/dress.png') },
          {name:'', title:'010-80720072', src:require('../../assets/phone.png') }
        ],
        dblink:[
          {name:'', src:'http://sbj.cnipa.gov.cn/'},
          {name:'', src:'http://www.samr.gov.cn/'},
          {name:'', src:'http://www.cnipa.gov.cn/'},
          {name:'', src:'http://wsjs.saic.gov.cn/txnT01.do?Flt3WMZ5=qmMADAhdCB1uwlnobEuH9XLubsK0B_WxrYf.Gm3Doln0.WKYa9W1GcqLKd__Cmwa3Zgnyc_sTwynaxX0qlkxcCRJdXghlp_grp9QjjCZI5M4NjE9YVTRO0HXnomkD0INo6taZYRE4zYEsYmJEVUissi7arA'},
          {name:'', src:'http://www.sac.gov.cn/'},
          {name:'', src:'http://www.cnca.gov.cn/'},
          {name:'', src:'http://www.china-cas.org/'}
        ]
      }
    },
    filters: {
      ellipsis(value) {
        if (!value) return ''
        if (value.length > 16) {
          return value.slice(0, 8) + '...'
        }
        return value
      }
    },
    methods: {
      login(){
        this.$router.push({
          path: '/login',
        })
      },
      zhuc() {
        this.$router.push({
          path: '/zhuc'
        })
      },
    }
  }
</script>
<style lang="less" scoped>
  a,a:hover{
    color: #666666;
  }
  .sever_foot{
    width: 1200px;
    margin: 0 auto;
    p{
      display: block;
      height: 60px;
      margin-bottom: 15px;
    }
  }
  .spanlink{
    display: inline-block;
    padding: 10px 20px 20px 20px;
    color: #666666;
    font-size: 13px;
  }
  p{
    text-align: center;
    img{
      height: 20px;
      width: 25px;
      display: block;
      margin: 20px auto 10px auto;
    }
    .name{
      font-size: 15px;
      font-weight: bold;
      color: #535353;
      display: block;
      width: 100%;
      height: 30px;
      line-height: 30px;
      text-align: center;
    }
    .title{
      font-size: 15px;
      font-weight: bold;
      color: #666;
      display: block;
      width: 100%;
      height: 20px;
      line-height: 20px;
      text-align: center;
    }
  }
</style>



