<template>
	<div class="layout" v-model="products">
    <topMeau></topMeau>
    <topHeader></topHeader>
    <div class="top_banner">
      <p >
        <span >{{products.name}}</span>
        <u class="urltitle" @click="shangbk">
          {{areaCode}}</u> >
        <u class="urltitle" @click="shangall">
          {{areaCode}}</u>>
        <u class="urltitle" @click="shangjienlei">{{areaCode}}{{categoryFir}}</u>>
        <b class="urltitle" >{{products.name}}</b>
      </p>
    </div>
		<Content style="margin-top: 20px">
			<div class="hm_product_num">
        <span class="buyseve">
        <buyservice :price="price"  :flag="oldflag" ></buyservice>
        </span>
        <p class="imgbg"><img :src="products.bannerUrl" class="img" /></p>
				<Row type="flex" :gutter="24" class="de_cont">
					<Col span="8">
					<p>: {{products.price}}</p>
					</Col>
					<Col span="5">
					<p>: {{servicePrice}}</p>
					</Col>
					<Col span="7">
					<p>: {{products.areaCode}}</p>
					</Col>
					<Col span="4">
					<p>:  {{products.id}}</p>
					</Col>
				</Row>

				<p class="line"></p>
				<Row type="flex" :gutter="24" class="de_cont">
					<Col span="8">
					<p>: {{products.firstNoticeNo}}</p>
					</Col>
					<!--<col span="12">
					<p>: <br/> {{products.registerNoticeNo}}</p>
					</col>-->
					<Col span="5">
					<p>: {{products.applyRange}}</p>
					</Col>
					<!--<col span="12">
					<p>: <br/> {{products.expireTime}}</p>
					</col>-->
					<Col span="7">
					<p>: {{products.deadlineyear}}</p>
					</Col>
					<Col span="4">
					<p>: {{products.publicFlag == 'Y' ? '':''}}</p>
					</Col>
					<Button v-if="phonevalue=='true'" type="primary" shape="circle">{{products.customerMobile}}</Button>
				</Row>

				<p class="line"></p>
				<div class="de_cont2 bottom_p">
					<span class="title"></span>
					<span class="smal-title"></span>
          <img src="../../assets/sb1.jpg" class="img2" />
					<span class="title2">1.</span>
					<span>
					
				</span>
					<span class="title2">2.</span>
					<span>
					</span>
					<span class="title2">3.</span>
					<span>
					5959
				</span>
					<span class="title2">4.</span>
					<span>
					//
				</span>
					<span class="title2">
						5.</span>
					<span>
					1-3,6-10
				</span>
          <img src="../../assets/nr2.jpg" />
          <img src="../../assets/nr3.jpg" />
          <img src="../../assets/nr4.jpg" />
          <img src="../../assets/nr5.jpg" />
				</div>
			</div>
		</Content>
		<Modal v-model="modal12" title="" @on-ok="ok" @on-cancel="cancel" v-if="products.status===0">
			<Row>
				<Col span="12">
				<Rate show-text v-model="valueText" />
				</Col>
			</Row>
		</Modal>
	</div>
</template>

<script>
	import buyservice from './../public/buyservice.vue';
  import topHeader from './../public/top.vue';
  import topMeau from './../public/top_meau.vue';
	import {
		brandgetById,Collectlist
	} from '../../api/api';
	export default {
		name: 'sb_details',
		data() {
			return {
				products: [],
				modal12: false,
				valueText: 0,
        price:this.price,
        oldflag:'',
        areaCode:this.$route.query.areaCode,
        categoryFir:this.$route.query.categoryFir,
        phonevalue:sessionStorage.phonevalue,
        servicePrice:sessionStorage.brand_fee,
        applyType: this.$route.query.applyType,
        id: this.$route.query.id
			}
		},
		components: {
      buyservice,
      topHeader,
      topMeau
		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			//
			getProducts() {
				this.listLoading = true;
				//NProgress.start();
				let applyType = this.$route.query.applyType;
				let para = {
					id: this.$route.query.id,
					applyType:applyType
				};
				brandgetById(para).then((res) => {
					this.products = res.data.result;
          this.price = this.products.price;
          this.id = this.products.id;
          let para = {
            customerId: sessionStorage.customerId,
            actionType:'2',
            contentType:'1'
          };
          Collectlist(para).then((res) => {
            this.sb_list = res.data.result.result.brands;
            let charr = []
            this.sb_list.forEach(value => {
              if (this.id ===value.id){
                this.flag = true
              } else {
                this.flag = false
              }
              charr.push(this.flag)
            })
            if(charr.indexOf(true) !== -1){
              this.oldflag=true
            } else{
              this.oldflag=false
            }
            this.listLoading = false;
            //NProgress.done();
          })
          this.listLoading = false;
          //NProgress.done();
        }).catch((err) => {
          //console.log(err);
        })
			},
			ok() {
				this.$Message.info('Clicked ok');
			},
			cancel() {
				this.$Message.info('Clicked cancel');
			},
      shangbk(){
        this.$router.push({
          path: '/product',
          query: {
            areaCode: this.areaCode
          }
        })
      },
      shangall(){
        this.$router.push({
          path: '/sb_all',
          query: {
            areaCode: this.areaCode
          }
        })
      },
      shangjienlei(){
        this.$router.push({
          path: '/sb_list',
          query: {
            areaCode: this.areaCode,
            categoryFir: this.categoryFir,
            inlandFlag: 'Y',
            sale: '1'
          }
        })
      }
		},
    watch: {
      'oldflag': function(newVal) {
        this.oldflag = newVal
      }
    },
		props: {},
		mounted() {
			this.getProducts();
		}
	}
</script>

<style lang="less" scoped>
  .urltitle{
    font-size: 14px;
    cursor: pointer
  }
	.top {
		margin-top: 200px;
	}

	.hm_product_num {
    position: relative;
		width: 1200px;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
    .imgbg{
      width: 100%;
      height: 350px;
      display: flex;
      overflow: hidden;
      align-items: center;
      .img {
        width: 100%;
        display: block;
      }
    }
    .buyseve{
      width: 280px;
      position: absolute;
      right: 20px; top: 20px;
      padding: 10px 20px;
      background-color: #ffffff;
      border-radius: 30px
    }
		.de_cont {
			padding: 20px;
		}
		span {
			display: block;
			text-align: left;
		}
    button{
      width: 90%;
      margin: 20px auto;
    }
		p {
			font-size: 15px;
			color: #000000;
			line-height: 22px;
			text-align: left;
			padding: 7px;
		}
		.line {
			width: 100%;
			background-color: #D7D7D7;
			height: 1px;
			padding: 0;
		}
		.de_cont2 {
			padding: 20px;
			.title {
				font-size: 28px;
				color: #000000;
				font-weight: bold;
				height: 45px;
				line-height: 45px;
				text-align: center;
			}
			.smal-title {
				font-size: 13px;
				color: #9B9B9B;
				text-align: center;
			}
			.title2 {
				font-size: 18px;
				color: #2B2EF5;
				line-height: 18px;
				font-weight: bold;
				margin-top: 25px;
				margin-bottom: 8px;
			}
			span {
				font-size: 14px;
				color: #000000;
				line-height: 20px;
			}
		}
	}
</style>



