<template>
  <div class="layout">
    <topMeau></topMeau>
    <topHeader></topHeader>
    <span class="topmibanner"><img src="../../assets/mibanner7.jpg"/></span>
    <div class="top_banner">
      <p >
        <span> </span>
         </p>
    </div>
		<Content class="content6 top_p">
			<div class="hm_product_num">
				<Tabs value="name1">
					<TabPane label="" name="name1" span="8">

						<ul>
							<li class="item" v-model="product_list" @click='sb_qg(item.id)' v-for="(item, name, index) in product_list">
								<p class="title">{{item.title | ellipsis}}</p>
								<span><p class="small_bh">{{item.id}}</p>
									<p class="price">{{item.createTime}}</p>
									</span>
							</li>
              <li span="24" v-if="product_list.length > 0"><Nomore></Nomore></li>
              <Nochild v-if="product_list.length === 0"></Nochild>
						</ul>

					</TabPane>
					<TabPane label="" name="name2">
						<ul>
							<li class="item" v-model="product_zllist"  @click='zl_qg(item.id)' v-for="(item, name, index) in product_zllist">
								<p class="title">{{item.title | ellipsis}}</p>
								<span><p class="small_bh">{{item.id}}</p>
									<p class="price">{{item.createTime}}</p>
									</span>
							</li>
              <li span="24" v-if="product_list.length > 0"><Nomore></Nomore></li>
              <Nochild v-if="product_list.length === 0"></Nochild>
						</ul>
					</TabPane>
				</Tabs>
			</div>
		</Content>
	</div>
</template>

<script>
  import Nomore from "../public/Nomore";
  import Nochild from "../public/Nochild";
  import topHeader from './../public/top.vue';
  import topMeau from './../public/top_meau.vue';
	import { Collectlist } from '../../api/api';
	export default {
		name: 'qiugou',
		data() {
			return {
				product_list:[],
				product_zllist:[],
			}
		},
		components: {
      topHeader,topMeau,Nomore,Nochild
		},
    filters: {
      ellipsis(value) {
        if (!value) return ''
        if (value.length > 13) {
          return value.slice(0, 13) + '...'
        }
        return value
      }
    },
		methods: {
			prev() {
				this.$router.go(-1)
			},
			sb_qg(id) {
				this.$router.push({
					path: '/sb_qg',
					query:{
						id:id,
				        actionType:'2',
				        contentType:'1',
					}
				})
			},
			zl_qg(id) {
				this.$router.push({
					path: '/zl_qg',
					query:{
						id:id,
				        actionType:'1',
				        contentType:'1',
					}
				})
			},//
			getProducts() {
				let para = {
				actionType:'1',
				contentType:'1',
				};
				this.listLoading = true;
				//NProgress.start();
				Collectlist(para).then((res) => {
					this.product_list = res.data.result.result;
					console.log(this.product_list);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
				let prams = {
				actionType:'1',
				contentType:'2',
				};
				this.listLoading = true;
				//NProgress.start();
				Collectlist(prams).then((res) => {
				  console.log(res)
					this.product_zllist = res.data.result.result;
					console.log(this.product_zllist);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},

		},
		props: {},
		mounted() {
			this.getProducts();
		}
	}
</script>

<style lang="less" scoped>
	.hm_product_num {
		width: 1200px;
		display: block;
		height: auto !important;
		min-height: 20px;
		overflow: hidden;
		li {
			flex: 2;
			font-size: 18px;
			color: #000000;
			width: 100%;
			line-height: 22px;
			text-align: left;
			padding: 20px 0;
			border-bottom: 1px solid #E6E6E6;
			span {
				width: 100%;
				display: block;
				width: 100%;
				display: flex;
				justify-content: space-between;
				line-height: 30px;
				font-size: 15px;
				color: #666666;
				.small_bh {
					text-align: left;
					flex: 1;
					align-items: left;
				}
				.price {
					text-align: right;
					flex: 1;
					align-items: right;
				}
			}
		}
	}
</style>



